import MenuIcon from '@material-ui/icons/Menu';
import '../index.css';
import { useState, useLayoutEffect } from 'react';

import TopBarElements from "../TopBarElements/TopBarElements";

function SidebarComponent() {

  const [isButtonClicked, changeButtonState] = useState(false);
  const [width] = useWindowSize();

  function useWindowSize() {
    const [size, setSize] = useState([0, 0]);
    useLayoutEffect(() => {
      function updateSize() {
        setSize([window.innerWidth, window.innerHeight]);
      }
      window.addEventListener('resize', updateSize);
      updateSize();
      return () => window.removeEventListener('resize', updateSize);
    }, []);
    return size;
  }



  if(width <= 1000) {
    if(isButtonClicked) {

      return  ( 
        <div className="neumorphic-card" style={{height: "10%", margin: "12px"}}>
        <div className="neumorphic-button-unpressed" style={{height: "45px", width: "45px", margin: "5px", borderRadius: "4px",}}>
          <MenuIcon style={{padding: "10px"}} onClick={() => changeButtonState(false)}/>
        </div>
        </div>
        );

        } else {

        return ( 
        <div className="neumorphic-card" style={{height: "10%", margin: "12px"}}>
        <div className="neumorphic-button-pressed" style={{height: "45px", width: "45px", margin: "5px", borderRadius: "4px",}}>
          
        <MenuIcon style={{padding: "10px"}} onClick={() => changeButtonState(true)}/>
        </div>
        </div>
        );
    }
  } else {


    return ( 
      <div className="neumorphic-card" style={{marginLeft: "36px", marginRight: "36px", marginTop: "12px", marginBottom: "24px",}}>
      <div className="mainHeaderText" style={{
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      height: "100%",
      fontFamily: "Manrope",
      marginTop: "30px",
      marginBottom: "16px",
      fontSize: "60px",
      }}>Neumorphism</div>



<div style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: "100%",
        fontFamily: "Manrope",
        marginBottom: "50px",
        fontSize: "16px",
        }}>The forbidden jewel.</div>

        <center>
      <TopBarElements />
      </center>





      </div>
      );

  }
}

export default SidebarComponent;