
import Grid from '@material-ui/core/Grid';
import {useState } from 'react';

var selectedText;
var renderSearchData;
function findOutTheBook(text) {
    var p = data.filter(
        item => !item || item.name.toLowerCase().includes(text.toLowerCase())
    ).map(
        (item, index) =>  (
            <div key={index} >
            <a href={item.name} style={{color: "#000", fontFamily: "Manrope", textDecoration: "none"}}>
                {item.name}
            </a>
            </div>
        )
    );
    return p;
}




